.drop-zone-container {
    background-color: #F2F2F2 !important;
    height: 160px;
    position: relative;
    box-sizing: border-box;
    .cancelButton {
        position: absolute;
        right: 16px;
        top: 16px;
    }
    .imgContainer {
        height: 128px;
    }
    .dropZone {
        width: 100%;
        height: 100%;
        padding: 16px;
    }
}